.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, .5);
    opacity: 0;
    visibility: hidden;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 50;
    transition: .8s ease;
}

.popup._open {
    opacity: 1;
    visibility: visible;
}

.popup._open .popup__content {
    transform: translate(0px, 0px);
    opacity: 1;
}

.popup__body {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 10px;
}

.popup__content {
    background-color: #fff;
    color: #000;
    width: 100%;
    max-width: 728px;
    padding: 86px 65px 58px;
    position: relative;
    border-radius: 10px;
    opacity: 0;
    transform: translate(0px, -100%);
    transition: .8s ease;
}

.popup__close {
    position: absolute;
    top: 13px;
    right: -96px;
    background-color: transparent;
}

.popup__title {
    text-align: center;
    margin-bottom: 75px;
}