.loader {
    display: flex;
    gap: 10px;
}

.loader__icon {
    width: 24px;
    height: 24px;
    display: block;
    animation: .75s linear infinite loading;
}

@keyframes loading {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}
