.btn {
    text-align: center;
    background-color: #0500ff;
    font-size: 18px;
    font-weight: 500;
    color: #fff;
    transition: background-color .3s;
    display: block;
    cursor: pointer;
    padding: 20px;
    border-radius: 10px;
}

.btn:hover {
    background-color: #0500e4;
}

.btn_secondary {
    background-color: #ededed;
    color: #47515a;
}

.btn_secondary:hover {
    background-color: #e3e3e3;
}

.btn_remove {
    background-color: #ec071e;
    padding: 17px;
}

.btn_remove:hover {
    background-color: #d50d21;
}

.btn_remove::before {
    content: '';
    background: url("/src/assets/img/delete.svg") no-repeat;
    width: 24px;
    height: 24px;
    display: block;
}

.btn._disabled {
    pointer-events: none;
}

.btn .loader {
    justify-content: center;
}

.btn_preview,
.btn_edit {
    padding: 14px;
}

.btn_preview::before {
    content: '';
    background: url("/src/assets/img/preview.svg") no-repeat center;
    width: 30px;
    height: 30px;
    display: block;
}

.btn_edit {
    background-color: #10cf50;
}

.btn_edit:hover {
    background-color: #0ebe49;
}

.btn_edit::before {
    content: '';
    background: url("/src/assets/img/edit.svg") no-repeat center;
    background-size: 24px;
    width: 30px;
    height: 30px;
    display: block;
}