.file-btn input[type="file"] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    display: block;
    width: 0;
    height: 0;
}

.file-btn {
    font-size: 18px;
    font-weight: 500;
    background-color: #0500ff;
    border-radius: 10px;
    padding: 17px;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: .3s;
}

.file-btn__content { 
    display: flex;
    align-items: center;
    gap: 10px;
}

.file-btn__content::after {
    content: '';
    background: url("/src/assets/img/upload-file.svg") no-repeat center/cover;
    background-size: 24px;
    width: 24px;
    height: 24px;
    display: block;
}

.file-btn._loading .file-btn__content::after {
    content: none;
}

.file-btn._not-tests {
    background-color: #ec071e;
}

.file-btn._not-tests:hover {
    background-color: #d50d21;
}