.accordance__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 40px;
}

.accordance-item {
    display: flex;
    align-items: center;
    gap: 10px;
}

.accordance-item__wrapper {
    display: flex;
    align-items: center;
}

.accordance-item__number {
    margin-right: 10px;
}

.accordance-item__box {
    display: flex;
    align-items: center;
    gap: 20px;
    background-color: #fff;
    box-shadow: 0 0 0 1px rgba(63, 63, 68, 0.05),
        0 1px 3px 0 rgba(34, 33, 81, 0.5);
    border-radius: 4px;
    list-style: none;
    color: #000;
    padding: 7px;
    hyphens: auto;
}

.accordance-item__wrapper,
.accordance-item__box {
    width: 100%;
    max-width: 400px;
}

.accordance-item__arrow {
    background-color: #0500ff;
    color: #fff;
    display: block;
    padding: 4px;
    border-radius: 5px;
    align-self: center;
}

.accordance-item._success .accordance-item__icon::after,
.accordance-item._failed .accordance-item__icon::after {
    content: '';
    width: 22px;
    height: 22px;
    display: block;
    background-repeat: no-repeat;
    background-size: contain;
}

.accordance-item._success .accordance-item__icon::after {
    background-image: url("/src/assets/img/success.svg");
}

.accordance-item._failed .accordance-item__icon::after {
    background-image: url("/src/assets/img/failed.svg");
}