.checkbox-item {
    list-style: none;
    color: #000;
    width: max-content;
}

.checkbox-item__input {
    display: none;
}

.checkbox-item:not(:last-child) {
    margin-bottom: 20px;
}

.checkbox-item__label {
    display: flex;
    align-items: center;
    font-size: 18px;
    gap: 15px;
    cursor: pointer;
}

.checkbox-item__label::before {
    content: '';
    width: 20px;
    height: 20px;
    display: inline-block;
    border-radius: 4px;
    border: 5px solid #fff;
    outline: 1px solid #0500ff;
    background-color: #fff;
}

.checkbox-item__label::before {
    border-width: 3px;
}

.checkbox-item._checked .checkbox-item__label:before {
    border-color: #0500ff;
    background-color: #0500ff;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.checkbox-item._success .checkbox-item__icon::after,
.checkbox-item._failed .checkbox-item__icon::after {
    content: '';
    width: 22px;
    height: 22px;
    display: block;
    background-repeat: no-repeat;
    background-size: contain;
}

.checkbox-item._success .checkbox-item__icon::after {
    background-image: url("/src/assets/img/success.svg");
}

.checkbox-item._failed .checkbox-item__icon::after {
    background-image: url("/src/assets/img/failed.svg");
}