.radio-item {
    list-style: none;
    color: #000;
    width: max-content;
}

.radio-item__input {
    display: none;
}

.radio-item:not(:last-child) {
    margin-bottom: 20px;
}

.radio-item__label {
    display: flex;
    align-items: center;
    font-size: 18px;
    gap: 15px;
    cursor: pointer;
}

.radio-item__label::before {
    content: '';
    width: 20px;
    height: 20px;
    display: inline-block;
    border-radius: 50%;
    border: 5px solid #fff;
    outline: 1px solid #0500ff;
    background-color: #fff;
}

.radio-item._checked .radio-item__label {
    border-color: #0500ff;
}

.radio-item._checked .radio-item__label:before {
    border-color: #0500ff;
    background-color: #fff;
}

.radio__clear-btn {
    align-self: flex-start;
}

.radio-item._success .radio-item__icon::after,
.radio-item._failed .radio-item__icon::after {
    content: '';
    width: 22px;
    height: 22px;
    display: block;
    background-repeat: no-repeat;
    background-size: contain;
}

.radio-item._success .radio-item__icon::after {
    background-image: url("/src/assets/img/success.svg");
}

.radio-item._failed .radio-item__icon::after {
    background-image: url("/src/assets/img/failed.svg");
}