.countdown {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.countdown__message {
    display: block;
    color: #4b4b4b;
    margin-right: 18px;
}

.countdown__time {
    font-weight: 700;
    font-size: 40px;
    display: block;
    color: #0050cf;
    width: 220px;
}