.score-circle__scores {
    width: 100px;
    height: 100px;
    background-color: #d9d9d9;
    border-radius: 50%;
    font-weight: 500;
    font-size: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    cursor: pointer;
}

.score-circle._success .score-circle__scores {
    background-color: #20ac7a;
}

.score-circle._failed .score-circle__scores {
    background-color: #dc4e67;
}

.score-circle._no-checked .score-circle__scores {
    background-color: #0500fe;
    pointer-events: all !important;
}

.score-circle._disable .score-circle__scores {
    pointer-events: none !important;
}