.draggable {
    display: flex;
    align-items: center;
    gap: 20px;
    background-color: #fff;
    box-shadow: 0 0 0 1px rgba(63, 63, 68, 0.05),
        0 1px 3px 0 rgba(34, 33, 81, 0.15);
    border-radius: 4px;
    box-sizing: border-box;
    list-style: none;
    color: #000;
    padding-right: 20px;
    width: 100%;
    max-width: 400px;
    hyphens: auto;
}

.drag-handle {
    display: flex;
    width: 12px;
    padding: 15px;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    touch-action: none;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    outline: none;
    appearance: none;
    background-color: transparent;
    -webkit-tap-highlight-color: transparent;
}

.drag-handle:hover {
    background-color: rgba(0, 0, 0, 0.05);
}

.drag-handle:focus-visible {
    box-shadow: 0 0px 0px 2px #4c9ffe;
}

.drag-handle svg {
    flex: 0 0 auto;
    margin: auto;
    height: 100%;
    overflow: visible;
    fill: #919eab;
}