    gap: 20px;
    margin-top: 10px;
}

.ticket-item__question>div:not(:last-child) {
    margin-bottom: 26px;
}

.ticket-item:not(:last-child) {
    margin-bottom: 60px;
}

.ticket-item__add {
    background-color: #259812;
}

.ticket-item__add:hover {
    background-color: #248d13;
}

.ticket-item__remove {
    background-color: #ec071e;
}

.ticket-item__remove:hover {
    background-color: #e70a20;
}

.archive__title {
    margin-bottom: 20px;
}

.archive__form {
    max-width: 600px;
}


@media print {

    .header,
    .footer,
    .testing-results__container {
        display: none;
    }

    .print {
        display: block;
    }

    body {
        font-family: 'Times New Roman', Times, serif;
        display: block;
    }

}


.p-toast .p-toast-message .p-toast-message-content {
    padding: 1rem;
}

.p-toast .p-toast-message .p-toast-message-content .p-toast-message-text {
    margin: 0 0 0 1rem;
}

.p-toast .p-toast-message.p-toast-message-success {
    background: rgba(228, 248, 240, 0.7);
    border: solid #1ea97c;
    border-width: 0 0 0 6px;
    color: #1ea97c;
}

.p-toast .p-toast-message.p-toast-message-error {
    background: rgba(255, 231, 230, 0.7);
    border: solid #ff5757;
    border-width: 0 0 0 6px;
    color: #ff5757;
}

.p-toast .p-toast-message {
    margin: 0 0 1rem 0;
}