.sortable-item {
  display: flex;
  align-items: center;
  gap: 20px;
}

.sortable-item__body {
  display: flex;
  align-items: center;
  gap: 20px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 0 calc(1px / var(--scale-x, 1)) rgba(63, 63, 68, 0.05),
    0 1px calc(3px / var(--scale-x, 1)) 0 rgba(34, 33, 81, 0.15);
  border-radius: calc(4px / var(--scale-x, 1));
  box-sizing: border-box;
  list-style: none;
  color: #000;
}

.sortable-item__drag-handle {
  display: flex;
  width: 12px;
  padding: 15px;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  touch-action: none;
  cursor: var(--cursor, pointer);
  border-radius: 5px;
  border: none;
  outline: none;
  appearance: none;
  background-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

.sortable-item__drag-handle:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.sortable-item__drag-handle:focus-visible {
  box-shadow: 0 0px 0px 2px #4c9ffe;
}

.sortable-item__drag-handle svg {
  flex: 0 0 auto;
  margin: auto;
  height: 100%;
  overflow: visible;
  fill: #919eab;
}

.sortable-item._success .sortable-item__icon::after,
.sortable-item._failed .sortable-item__icon::after {
    content: '';
    width: 22px;
    height: 22px;
    display: block;
    background-repeat: no-repeat;
    background-size: contain;
}

.sortable-item._success .sortable-item__icon::after {
    background-image: url("/src/assets/img/success.svg");
}

.sortable-item._failed .sortable-item__icon::after {
    background-image: url("/src/assets/img/failed.svg");
}