.datepicker {
    width: 100%;
    max-width: 447px;
    border: 1px solid #bcbcbc;
    border-radius: 5px;
    padding: 17px 38px 15px;
    font-size: 18px;
    line-height: 1.2;
    color: #000;
}

.datepicker {
    max-width: 300px;
    cursor: pointer;
}

.datepicker-wrapper {
    width: max-content;
}

.react-datepicker__view-calendar-icon input {
    padding: 16px 56px !important;
}

.react-datepicker__calendar-icon {
    display: none;
}

.react-datepicker__input-container::before {
    content: '';
    background: url('/src/assets//img/calendar.svg');
    width: 20px;
    height: 22px;
    display: block;
    position: absolute;
    top: 49%;
    transform: translateY(-50%);
    left: 19px;
}

.react-datepicker__input-container::after {
    content: '';
    background: url('/src/assets/img/arrow-down.svg') no-repeat;
    width: 21px;
    height: 21px;
    transform: rotate(0deg);
    z-index: 10;
    transition: .5s;
}

.react-datepicker__input-container::after {
    position: absolute;
    top: 49%;
    transform: translateY(-50%);
    /* left: 245px; */
    right: 30px;
}

.datepicker-wrapper.open .react-datepicker__input-container::after {
    transform: translateY(-50%) rotate(-180deg);
}