.droppable:not(#draggable-container) {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: #fff;
    box-shadow: 0 0 0 1px rgba(63, 63, 68, 0.05),
        0 1px 3px 0 rgba(34, 33, 81, 0.5);
    border-radius: 4px;
    list-style: none;
    color: #000;
    padding: 7px;
    min-height: 34px;
    transition: 0.3s;
}

#draggable-container {
    display: flex;
    flex-wrap: wrap;
    border-radius: 4px;
    padding: 12px;
    max-width: initial;
    min-height: 50px;
    gap: 20px;
    box-shadow: 0 0 0 1px rgba(63, 63, 68, 0.05),
    0 1px 3px 0 rgba(34, 33, 81, 0.5);
    transition: 0.3s;
}

.droppable:not(#draggable-container)._is-over,
#draggable-container._is-over {
    box-shadow: 0 0 0 1px #0500ff, 0 1px 3px 0 #0500ff;
}

