.nav__link  {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    transition: background-color .3s;
    display: block;
    cursor: pointer;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    border: 1px solid #0500ff;
    color: #0500ff;
}

.nav__link:hover,
.nav__link._active {
    background-color: #0500ff;
    color: #fff;
}